.mo-header {
    display: none;
}
.radio-wrap {position: relative;
    [type="radio"] {margin: 0;width: 1px;height: 1px;padding: 0;border: 0;overflow: hidden;margin: -1px;clip-path: inset(50%);
        &:checked {
            &+label {
                .check-icon {
                    background-color: #86b7fe;
                    border-color: #86b7fe;
                    &::before {
                        background-color: #86b7fe;
                        border-color: #FFF;
                    }
                }
            }
        }
    }
    label {
        padding-left: 1.5rem;
        position: relative;
        cursor: pointer;
        .check-icon {width: 1.5rem; height: 1.5rem; border-radius: 50%; background-color: #fff; border: 1px solid gray; position: absolute; left: 0; top: 50%; transform: translateY(-50%);
            &::before {
                content: "";
                position: absolute;
                box-sizing: border-box;
                width: 30%;
                height: 55%;
                left: 50%;
                top: 50%;
                transform: translateX(-50%) translateY(-70%) rotateZ(40deg);
                border-right: 1.7px solid gray;
                border-bottom: 1.7px solid gray;
            }
        }
    }
}

.check-wrap {position: relative;
    [type="checkbox"] {margin: 0;width: 1px;height: 1px;padding: 0;border: 0;overflow: hidden;margin: -1px;clip-path: inset(50%);
        &:checked {
            &+label {
                .check-icon {
                    background-color: #86b7fe;
                    border-color: #86b7fe;
                    &::before {
                        background-color: #86b7fe;
                        border-color: #FFF;
                    }
                }
            }
        }
    }
    label {
        padding-left: 1.2rem;
        position: relative;
        cursor: pointer;
        .check-icon {width: 1.2rem; height: 1.2rem; background-color: #fff; border: 1px solid gray; position: absolute; left: 0; top: 50%; transform: translateY(-50%);
            &::before {
                content: "";
                position: absolute;
                box-sizing: border-box;
                width: 30%;
                height: 55%;
                left: 50%;
                top: 50%;
                transform: translateX(-50%) translateY(-70%) rotateZ(40deg);
                border-right: 1.7px solid gray;
                border-bottom: 1.7px solid gray;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .pc-header {
        display: none;
    }
    .header {
        height: 56px;
    }
    .mo-header {
        display: block;
    }

    .container {
        width: 100%;
        max-width: none;
    }
    .mo-side {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        background-color: #F8FAFC;
        z-index: 9999;
    }
    .side-header {
        height: 56px;
        padding: .5rem;
        border-bottom: 1px solid #e5e7eb;
    }
    .nav-section-title { 
        font-size: 1rem;
        font-weight: 600;
        color: #9ca3af;
        text-transform: uppercase;
        letter-spacing: 0.04em;
        margin-bottom: 4px;
    }

    .sidebar-nav a {
        display: block;
        padding: 8px 0;
        font-size: 1.2rem;
        color: #111827;
        text-decoration: none;
    }

}

.modal {position: fixed; width: 100vw; height: 100vh; background-color: #e8e8e866; z-index: 9999; top: 0; left: 0;
    &-dialog {width: 25%; min-height: 200px; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); background-color: #ffffff; padding: 1rem; border-radius: .375rem; display: flex; flex-direction: column; justify-content: space-between;
        .tlt {font-size: 1.5rem; font-weight: 700;}
        .modal-header {margin-bottom: 1rem;}
        .modal-body {margin-bottom: 1rem;}
        .modal-footer {
            .btn-wrap {display: flex;}
            .btn {padding: .5rem 1rem; font-size: 1rem; width: 100%;}
            .close-btn {background-color: $danger; color: #fff;}
        }
    }
}